import * as types from '../constants/car';

const initialState = {
    listCar: []
}

const reducer = (state = initialState, action) => {
    switch (action.type) {
        case types.FETCH_LIST_CAR: {
            return {
                ...state,
                listUser: [],
            }
        }
        case types.FETCH_LIST_CAR_SUCCESS: {
            const { data } = action.payload;
            const cars = Object.values(data)
            return {
                ...state,
                listCar: cars,
            }
        }
        case types.FETCH_LIST_CAR_FAILED: {
            const { error } = action.payload;
            console.log(error);

            return {
                ...state,
                listUser: [],
            }
        }
        case types.DELETE_CAR:

            return {
                ...state
            }
        case types.DELETE_CAR_SUCCESS:
            return {
                ...state
            }
        case types.DELETE_CAR_FAILED:
            return {
                ...state
            }
        default:
            return state;
    }
}

export default reducer;