import { fork, call, put, takeEvery, all, delay } from 'redux-saga/effects';
import * as types from '../constants/auth';
import * as loginApi from '../api/auth';
import * as actionLogin from '../actions/auth';
import * as actionUi from '../actions/ui';



function* watchLoginAccount({payload}) {

    yield put(actionUi.showLoading());
    const { account } = payload;
    const { username,password } = account;
    const res = yield call(loginApi.login, {phone:username,password});
    if (res.status === 200) {
        if(res.data){
            yield put(actionLogin.loginAccountSuccess(res.data));
            yield put(actionUi.showAlert(1,'ÄÄng nháº­p thÃ nh cÃ´ng'));
        }
    }
    else {
        console.log(res.error);
    }
    yield put(actionUi.hideLoading());
    yield delay(3000);
    yield put(actionUi.hideAlert());

}

export function* login() {
    yield takeEvery(types.LOGIN_ACCOUNT, watchLoginAccount);
}

function* rootSaga() {
    yield all([
        fork(login)
    ]);

}


export default rootSaga;
