import React, { Component } from "react";
import { connect } from "react-redux";
import { withStyles } from "@material-ui/styles";
import { Modal } from "@material-ui/core";
import CloseIcon from "@material-ui/icons/Clear";
import styles from "./styles";
import * as actionModal from "../../actions/modal";

class ModalForm extends Component {
  render() {
    const { classes, open, data, component, title } = this.props;

    return (
        <Modal open={open} onClose={this.props.hideModal}>
          <div className={classes.modal}>
            <div className={classes.header}>
              <span className={classes.title}>{title}</span>
              <CloseIcon
                className={classes.icon}
                onClick={this.props.hideModal}
              />
            </div>
            <div className={classes.content}>{component}</div>
          </div>
        </Modal>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    open: state.modal.showModal,
    data: state.modal.data,
    component: state.modal.component,
    title: state.modal.title,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    hideModal: () => {
      dispatch(actionModal.hideModal());
    },
  };
};

export default withStyles(styles)(
  connect(mapStateToProps, mapDispatchToProps)(ModalForm)
);
