import React, { Component } from "react";
import { Route, Switch, Redirect } from "react-router-dom";
import { connect } from "react-redux";

import { withStyles } from "@material-ui/core/styles";
import Box from "@material-ui/core/Box";
import Typography from "@material-ui/core/Typography";
import Container from "@material-ui/core/Container";
import Link from "@material-ui/core/Link";

import Home from "./Home";
import UserList from "../../components/UserList";
import CarList from "../../components/CarList";
import NavMenu from "../NavMenu";
import styles from "./styles";
import Reports from "./Reports";

function Copyright() {
  return (
    <Typography variant="body2" color="textSecondary" align="center">
      {"Copyright Â© "}
      <Link color="inherit" href="https://material-ui.com/">
        Your Website
      </Link>{" "}
      {new Date().getFullYear()}
      {"."}
    </Typography>
  );
}

class Dashboard extends Component {
  render() {
    const { classes } = this.props;
    const  url = "/dashboard";
    const { access_token } = this.props;

    const redirect = access_token ? "" : <Redirect to="/signin" />;
    return (
      <div className={classes.root}>
        {redirect}
        <NavMenu />
        <main className={classes.content}>
          <div className={classes.appBarSpacer} />

          {/* container */}

          <Container maxWidth="lg" className={classes.container}>
            <Switch>
              <Route path={`${url}`} exact component={Home} />
              <Route path={`${url}/user`} component={UserList} />
              <Route path={`${url}/car`} component={CarList} />
              <Route path={`${url}/reports`} component={Reports} />
            </Switch>

            <Box pt={4}>
              <Copyright />
            </Box>
          </Container>
        </main>
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    account: state.auth,
  };
};

export default withStyles(styles)(connect(mapStateToProps, null)(Dashboard));
