import * as types from '../constants/auth';

export const loginAccount = (account) =>{
    return {
        type: types.LOGIN_ACCOUNT,
        payload:{
            account
        }
    } 
}

export const loginAccountSuccess = (data) =>{

    return {
        type: types.LOGIN_ACCOUNT_SUCCESS,
        payload:{
            data
        }
    } 
}

export const loginAccountFailed = (error) =>{
    return {
        type: types.LOGIN_ACCOUNT_SUCCESS,
        payload:{
            error
        }
    } 
}