import axiosService from '../commons/axiosService';

const urlBase = 'http://localhost:8080/api/admin/car';


export const getList = () => {
    return axiosService.get(urlBase);
}

export const postCar = (data) => {
    return axiosService.post(urlBase, data)
}

export const deleteCar = (id) => {
    const url = `${urlBase}/delete/${id}`;

    return axiosService.post(url);
}

export const updateCar = (id,data) =>{
    const url = `${urlBase}/${id}`;
    return axiosService.put(url,data);
} 



