import * as types from '../constants/user';

// láº¥y list User

export const fetchListUser = () =>{
    return {
        type: types.FETCH_LIST_USER
    } 
}

export const fetchListUserSuccess = (data) =>{
    return {
        type: types.FETCH_LIST_USER_SUCCESS,
        payload:{
            data
        }
    } 
}

export const fetchListUserFailed = (error) =>{
    return {
        type: types.FETCH_LIST_USER_SUCCESS,
        payload:{
            error
        }
    } 
}

// ThÃªm User má»i

export const addUser = newUser =>{
    return {
        type: types.ADD_USER,
        payload: {
            newUser
        }
    }
}

export const addUserSuccess = user =>{
    return {
        type: types.ADD_USER_SUCCESS,
        payload: {
            user
        }
    }
}

export const addUserFailed = err =>{
    return {
        type: types.ADD_USER_FAILED,
        payload: {
            err
        }
    }
}

// xÃ³a User

export const deleteUser = (id) =>{
    return {
        type: types.DELETE_USER,
        payload:{
            id
        }
    }
}

export const deleteUserSuccess = (id) =>{
    return {
        type: types.DELETE_USER_SUCCESS,
        payload:{
            id
        }
    }
}

export const deleteUserFailed = (err) =>{
    return {
        type: types.DELETE_USER_FAILED,
        payload:{
            err
        }
    }
}



// update user

export const updateUser = (id,newUser) =>{
    return {
        type: types.UPDATE_USER,
        payload:{
            id,
            newUser
        }
    }
}

export const updateUserSuccess = (id,newUser) =>{
    return {
        type: types.UPDATE_USER_SUCCESS,
        payload:{
            id,
            newUser
        }
    }
}

export const updateUserFailed = (err) =>{
    return {
        type: types.UPDATE_USER_FAILED,
        payload:{
            err
        }
    }
}
