import React, { Component } from "react";
import { connect } from "react-redux";

import { Button, TextField } from "@material-ui/core";

import { withStyles } from "@material-ui/styles";

import * as actionModal from "../../../actions/modal";

import styles from "./styles";

class UserForm extends Component {
  constructor(props) {
    super(props);
    this.state = {
      name: "",
      phone: "",
      email: "",
    };
  }

  componentDidMount() {
    const { user } = this.props;
    const { id, name, avatar, phone, email } = user;
    this.setState({
      name,
      phone,
      email,
    });
  }

  onSubmit = (id, newUser) => {
    this.props.updateUser(id, newUser);
  };

  onChange = (event) => {
    const { target } = event;
    const { name, value, size, type } = target;

    if (type === "file") {
      this.setState({
        avatar:target.files[0]
      });
    } else {
      this.setState({
        [name]: value,
      });
    }
  };

  render() {
    const { classes, user } = this.props;
    const { id, name, avatar, phone, email } = user;
    const newUser = this.state;
    return (
      <div>
        <input
          type="file"
          id="avatar"
          name="avatar"
          accept="image/png, image/jpeg"
          onChange={this.onChange}
        />
        <TextField
          id="standard-full-width"
          label="Name"
          style={{ margin: 8 }}
          name="name"
          value={this.state.name}
          fullWidth
          margin="normal"
          onChange={this.onChange}
          InputLabelProps={{
            shrink: true,
          }}
        />
        <TextField
          id="standard-full-width"
          label="Phone"
          name="phone"
          value={this.state.phone}
          style={{ margin: 8 }}
          fullWidth
          margin="normal"
          onChange={this.onChange}
          InputLabelProps={{
            shrink: true,
          }}
        />
        <TextField
          id="standard-full-width"
          label="Email"
          name="email"
          value={this.state.email}
          style={{ margin: 8 }}
          fullWidth
          margin="normal"
          onChange={this.onChange}
          InputLabelProps={{
            shrink: true,
          }}
        />
        <Button color="primary" onClick={this.props.hideModal}>
          Disagree
        </Button>
        <Button
          color="primary"
          autoFocus
          onClick={() => this.onSubmit(id, newUser)}
        >
          Agree
        </Button>
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return {};
};

const mapDispatchToProps = (dispatch) => {
  return {
    showModal: (user) => {
      dispatch(actionModal.showModal(user));
    },
    hideModal: () => {
      dispatch(actionModal.hideModal());
    },
  };
};

export default withStyles(styles)(
  connect(mapStateToProps, mapDispatchToProps)(UserForm)
);
