import React, { Component } from "react";
import { connect } from "react-redux";

import { Button, TextField } from "@material-ui/core";

import { withStyles } from "@material-ui/styles";

import * as actionModal from "../../../actions/modal";

import styles from "./styles";

class UserForm extends Component {
  constructor(props) {
    super(props);
    this.state = {
      avatar: null,
      name: "",
      phone: "",
      email: "",
      password: "",
    };
  }

  onSubmit = (newUser) => {
    this.props.addUser(newUser);
    this.props.hideModal();
  };

  onChange = (event) => {
    const { target } = event;
    const { name, value, type } = target;

    if (type === "file") {
      this.setState({
        avatar: target.files[0],
      });
    } else {
      this.setState({
        [name]: value,
      });
    }
  };

  render() {
    const { classes } = this.props;
    const newUser = this.state;
    return (
      <div>
        <div className="custom-file" style={{ margin: 8 }}>
          <input
            type="file"
            id="avatar"
            name="avatar"
            accept="image/png, image/jpeg"
            onChange={this.onChange}
          />
          <label className="custom-file-label" htmlFor="avatar">
            {this.state.avatar ? `${this.state.avatar.name}` : "choice image"}
          </label>
        </div>
        <TextField
          id="standard-full-width"
          label="Name"
          style={{ margin: 8 }}
          name="name"
          value={this.state.name}
          placeholder="user name"
          fullWidth
          margin="normal"
          onChange={this.onChange}
          InputLabelProps={{
            shrink: true,
          }}
        />
        <TextField
          id="standard-full-width"
          label="Phone"
          name="phone"
          value={this.state.phone}
          placeholder="your phone"
          style={{ margin: 8 }}
          fullWidth
          margin="normal"
          onChange={this.onChange}
          InputLabelProps={{
            shrink: true,
          }}
        />
        <TextField
          id="standard-full-width"
          label="Email"
          name="email"
          value={this.state.email}
          placeholder="email@something.com"
          style={{ margin: 8 }}
          fullWidth
          margin="normal"
          onChange={this.onChange}
          InputLabelProps={{
            shrink: true,
          }}
        />

        <TextField
          id="standard-full-width"
          label="Password"
          name="password"
          type="password"
          value={this.state.password}
          placeholder="**************"
          style={{ margin: 8 }}
          fullWidth
          margin="normal"
          onChange={this.onChange}
          InputLabelProps={{
            shrink: true,
          }}
        />
        <Button color="primary" onClick={this.props.hideModal}>
          Disagree
        </Button>
        <Button
          color="primary"
          autoFocus
          onClick={() => this.onSubmit(newUser)}
        >
          Agree
        </Button>
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return {};
};

const mapDispatchToProps = (dispatch) => {
  return {
    showModal: (user) => {
      dispatch(actionModal.showModal(user));
    },
    hideModal: () => {
      dispatch(actionModal.hideModal());
    },
  };
};

export default withStyles(styles)(
  connect(mapStateToProps, mapDispatchToProps)(UserForm)
);
