import * as types from '../constants/car';

// láº¥y list User

export const fetchListCar = () =>{
    return {
        type: types.FETCH_LIST_CAR
    } 
}

export const fetchListCarSuccess = (data) =>{
    return {
        type: types.FETCH_LIST_CAR_SUCCESS,
        payload:{
            data
        }
    } 
}

export const fetchListCarFailed = (error) =>{
    return {
        type: types.FETCH_LIST_CAR_SUCCESS,
        payload:{
            error
        }
    } 
}


export const deleteCar = (carId) =>{
    return {
        type: types.DELETE_CAR,
        payload:{
            carId
        }
    } 
}

export const deleteCarSuccess = (data) =>{
    return {
        type: types.DELETE_CAR_SUCCESS,
        payload:{
            data
        }
    } 
}

export const deleteCarFailed = (error) =>{
    return {
        type: types.DELETE_CAR_SUCCESS,
        payload:{
            error
        }
    } 
}