import * as types from "../constants/ui";

const initialState = {
  showLoading: false,
  showAlert: {
    option: 0,
    message: "",
  },
};

const reducer = (state = initialState, action) => {
  switch (action.type) {
    case types.SHOW_LOADING:
      return {
        ...state,
        showLoading: true,
      };
    case types.HIDE_LOADING:
      return {
        ...state,
        showLoading: false,
      };
    case types.SHOW_ALERT:
      const { option, message } = action.payload;
      return {
        ...state,
        showAlert: {
          option,
          message,
        },
      };
    case types.HIDE_ALERT:
      const showAlert = {
        option: 0,
        message: "",
      };
      return {
        ...state,
        showAlert
      };

    default:
      return state;
  }
};

export default reducer;
