import React, { Component } from "react";
import { withStyles } from "@material-ui/styles";
import { connect } from "react-redux";
import styles from "./styles";
import { Alert, AlertTitle } from "@material-ui/lab";

class GlobalAlert extends Component {
  render() {
    let xhtml = null;

    const { classes, showAlert } = this.props;
    const { option, message } = showAlert;
    switch (option) {
      case 1:
        xhtml = (
          <Alert severity="success" className={classes.alert}>
            <AlertTitle>Success</AlertTitle>
            {message} â <strong>check it out!</strong>
          </Alert>
        );
        break;
      case 2:
        xhtml = (
          <Alert severity="info" className={classes.alert}>
            <AlertTitle>Info</AlertTitle>
            {message} â <strong>check it out!</strong>
          </Alert>
        );
        break;
      case 3:
        xhtml = (
          <Alert severity="warning" className={classes.alert}>
            <AlertTitle>Warning</AlertTitle>
            {message} â <strong>check it out!</strong>
          </Alert>
        );
        break;
      case 4:
        xhtml = (
          <Alert severity="error" className={classes.alert}>
            <AlertTitle>Error</AlertTitle>
            {message} â <strong>check it out!</strong>
          </Alert>
        );
        break;

      default:
        return xhtml;
    }
    return <div className={classes.root}>{xhtml}</div>;
  }
}

const mapStateToProps = (state) => {
  return {
    showAlert: state.ui.showAlert,
  };
};

export default withStyles(styles)(connect(mapStateToProps, null)(GlobalAlert));
