import React, { Component } from "react";
import { NavLink } from "react-router-dom";

import clsx from "clsx";
import ListItem from "@material-ui/core/ListItem";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import ListItemText from "@material-ui/core/ListItemText";
import ListSubheader from "@material-ui/core/ListSubheader";
import DashboardIcon from "@material-ui/icons/Dashboard";
import PeopleIcon from "@material-ui/icons/People";
import BarChartIcon from "@material-ui/icons/BarChart";
import LayersIcon from "@material-ui/icons/Layers";
import AssignmentIcon from "@material-ui/icons/Assignment";
import DriveEtaIcon from "@material-ui/icons/DriveEta";
import ChevronLeftIcon from "@material-ui/icons/ChevronLeft";
import Divider from "@material-ui/core/Divider";
import List from "@material-ui/core/List";
import Drawer from "@material-ui/core/Drawer";
import IconButton from "@material-ui/core/IconButton";

import { withStyles } from "@material-ui/styles";

import styles from "./styles";

class NavListLink extends Component {
  render() {
    const { classes } = this.props;

    const listLink = [
      {
        to: "/dashboard",
        title: "Dashboard",
        icon: <DashboardIcon />,
        exact:true
      },
      {
        to: "/dashboard/car",
        title: "Quáº£n lÃ½ car",
        icon: <DriveEtaIcon />,
        exact:false
      },
      {
        to: "/dashboard/user",
        title: "Quáº£n lÃ½ User",
        icon: <PeopleIcon />,
        exact:false
      },
      {
        to: "/dashboard/reports",
        title: "Thá»ng kÃª",
        icon: <BarChartIcon />,
        exact:false
      },
      {
        to: "/dashboard/integrations",
        title: "Integrations",
        icon: <LayersIcon />,
        exact:false
      },
    ];

    let elmListLink = listLink.map((itemLink, index) => {
      const { title, to, icon, exact} = itemLink;
      return (
        <NavLink to={to} className={classes.listLink} exact={exact} key={index} activeClassName={classes.linkActive}>
          <ListItem button>
            <ListItemIcon>
              {icon}
            </ListItemIcon>
            <ListItemText primary={title} />
          </ListItem>
        </NavLink>
      );
    });

    return (
      <div>
        {/* NAVLINK-PRIMARY */}

        <List>
          {elmListLink}
        </List>

        <Divider />

        {/* NAVLINK-SECONDARY */}

        <List>
          <ListSubheader inset>Saved reports</ListSubheader>
          <ListItem button>
            <ListItemIcon>
              <AssignmentIcon />
            </ListItemIcon>
            <ListItemText primary="Current month" />
          </ListItem>
          <ListItem button>
            <ListItemIcon>
              <AssignmentIcon />
            </ListItemIcon>
            <ListItemText primary="Last quarter" />
          </ListItem>
          <ListItem button>
            <ListItemIcon>
              <AssignmentIcon />
            </ListItemIcon>
            <ListItemText primary="Year-end sale" />
          </ListItem>
        </List>
      </div>
    );
  }
}

export default withStyles(styles)(NavListLink);
