import { fork, take, call, put, takeEvery, takeLatest, delay, all } from 'redux-saga/effects';
import * as CarTypes from '../constants/car';
import * as actionCar from '../actions/car';
import * as actionUi from '../actions/ui';
import * as CarApi from '../api/car';


function* watchFetchListCarAction() {


    yield put(actionUi.showLoading());
    const res = yield call(CarApi.getList);
    if (res.status === 200) {
        yield put(actionCar.fetchListCarSuccess(res.data));
    }
    else {
        yield put(actionCar.fetchListCarFailed());
    }
    yield delay(300);
    yield put(actionUi.hideLoading());

}


function* watchDeleteCarAction({payload}) {
    const id = payload.carId
    yield put(actionUi.showLoading());
    const res = yield call(CarApi.deleteCar,id);
    if (res.status === 200) {
        yield put(actionCar.fetchListCar());
        yield put(actionUi.showAlert(1,"XÃ³a xe thÃ nh cÃ´ng"));
    }
    else {
        yield put(actionCar.fetchListCarFailed());
        yield put(actionUi.showAlert(4,"XÃ³a xe tháº¥t báº¡i"));
    }
    yield delay(3000);
    yield put(actionUi.hideAlert());

}



export function* todoCar() {
    yield takeEvery(CarTypes.FETCH_LIST_CAR, watchFetchListCarAction);
    yield takeEvery(CarTypes.DELETE_CAR, watchDeleteCarAction);

}

function* rootSaga() {
    yield all([
        fork(todoCar)
    ]);

}


export default rootSaga;
