import React, { Component } from "react";
import { connect } from "react-redux";
import { withStyles } from "@material-ui/styles";
import styles from "./styles";


import { MDBDataTable } from "mdbreact";

import * as actionModal from "../../actions/modal";
import * as actionCar from "../../actions/car";
import ActionCar from '../CarItem';

class CarList extends Component {
  componentDidMount() {
    this.props.fetchListCar();
  }

  deleteCar = (id) => {
    this.props.deleteCar(id);
    this.props.hideModal();
  };

  render() {
    const { cars } = this.props;

    const listCar = cars.map((car, index) => {
      const { license_plates, image, address, car_type, price } = car;
      return {
        license_plates: license_plates,
        image: (
          <img
            src={image}
            alt="thumbnail"
            className="img-thumbnail"
            width="100px"
          />
        ),
        address: address,
        type: car_type,
        price: price,
        action:  <ActionCar car={car} deleteCar={this.deleteCar}/>,
      };
    });


    const data = {
      columns: [
        {
          label: "Image",
          field: "image",
          sort: "asc",
          width: 150,
        },
        {
          label: "Biá»n sá»",
          field: "license_plates",
          sort: "asc",
          width: 200,
        },
        {
          label: "Address",
          field: "address",
          sort: "asc",
          width: 200,
        },
        {
          label: "Type",
          field: "type",
          sort: "asc",
          width: 100,
        },
        {
          label: "Price",
          field: "price",
          sort: "asc",
          width: 150,
        },
        {
          label: "Action",
          field: "action",
          sort: "asc",
          width: 100,
        },
      ],
      rows: [],
    };

    data.rows = listCar;

    return <MDBDataTable striped bordered small data={data}/>;
  }
}

const mapStateToProps = (state) => {
  return {
    cars: state.cars.listCar,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    
    hideModal: () => {
      dispatch(actionModal.hideModal());
    },
    fetchListCar: () => {
      dispatch(actionCar.fetchListCar());
    },
    deleteCar: (carId) =>{
      dispatch(actionCar.deleteCar(carId));
    }
  };
};

export default withStyles(styles)(
  connect(mapStateToProps, mapDispatchToProps)(CarList)
);
