import React, { Component } from "react";
import { connect } from "react-redux";

import UserForm from "./UserForm";

import {
  Card,
  CardHeader,
  CardMedia,
  CardContent,
  CardActions,
  Avatar,
  IconButton,
  Typography,
  Button,
  TextField,
} from "@material-ui/core";

import MoreVertIcon from "@material-ui/icons/MoreVert";
import DeleteIcon from "@material-ui/icons/Delete";
import CreateIcon from "@material-ui/icons/Create";
import { withStyles } from "@material-ui/styles";

import * as actionModal from "../../actions/modal";

import styles from "./styles";

class UserItem extends Component {
  onDeleteUser = (user) => {
    this.props.showModal(user);
    this.props.changeModalTitle("XÃ³a User nÃ y");
    this.props.changeModalContent(
      <div>
        <Button color="primary" onClick={this.props.hideModal}>
          Disagree
        </Button>
        <Button
          color="primary"
          onClick={() => this.props.deleteUser(user.id)}
          autoFocus
        >
          Agree
        </Button>
      </div>
    );
  };

  onUpdateUser = (user) => {
    this.props.showModal(user);
    this.props.changeModalTitle("Chá»nh sá»­a User");
    this.props.changeModalContent(
      <UserForm user={user} updateUser={this.props.updateUser} />
    );
  };

  render() {
    const { classes, user } = this.props;
    const { id, name, avatar, phone, email } = user;
    return (
      <Card className={classes.root}>
        <CardHeader
          avatar={
            <Avatar
              aria-label="recipe"
              className={classes.avatar}
              src={avatar}
            ></Avatar>
          }
          action={
            <IconButton aria-label="settings">
              <MoreVertIcon />
            </IconButton>
          }
          title={name}
          subheader={phone}
        />
        <CardMedia
          className={classes.media}
          image={avatar}
          title="Paella dish"
        />
        <CardContent>
          <Typography color="textSecondary" component="p">
            {email}
          </Typography>
        </CardContent>
        <CardActions disableSpacing>
          <IconButton
            aria-label="add to favorites"
            color="secondary"
            onClick={() => this.onDeleteUser(user)}
          >
            <DeleteIcon />
          </IconButton>
          <IconButton
            aria-label="share"
            color="primary"
            onClick={() => this.onUpdateUser(user)}
          >
            <CreateIcon />
          </IconButton>
        </CardActions>
      </Card>
    );
  }
}

const mapStateToProps = (state) => {
  return {};
};

const mapDispatchToProps = (dispatch) => {
  return {
    showModal: (user) => {
      dispatch(actionModal.showModal(user));
    },
    hideModal: () => {
      dispatch(actionModal.hideModal());
    },
    changeModalTitle: (title) => {
      dispatch(actionModal.changeModalTitle(title));
    },
    changeModalContent: (component) => {
      dispatch(actionModal.changeModalContent(component));
    },
  };
};

export default withStyles(styles)(
  connect(mapStateToProps, mapDispatchToProps)(UserItem)
);
