import { fork, call, put, takeEvery, delay, all } from "redux-saga/effects";
import * as UserTypes from "../constants/user";
import * as actionUser from "../actions/user";
import * as actionUi from "../actions/ui";
import * as UserApi from "../api/user";

function* watchFetchListUserAction() {
  yield put(actionUi.showLoading());
  const res = yield call(UserApi.getList);
  if (res.status === 200) {
    yield put(actionUser.fetchListUserSuccess(res.data));
  } else {
    yield put(actionUser.fetchListUserFailed());
  }
  yield delay(300);
  yield put(actionUi.hideLoading());
}

function* watchDeleteUserAction({ payload }) {
  yield put(actionUi.showLoading());
  const { id } = payload;
  const res = yield call(UserApi.deleteUser, id);

  if (res.status === 200) {
    yield put(actionUser.fetchListUser());
    yield put(actionUser.deleteUserSuccess());
    yield put(actionUi.showAlert(1, "XÃ³a thÃ nh viÃªn thÃ nh cÃ´ng"));
  } else {
    yield put(actionUser.deleteUserFailed());
    yield put(actionUi.showAlert(4));
  }
  yield delay(3000);
  yield put(actionUi.hideAlert());
}

function* watchUpdateUserAction({ payload }) {
  yield put(actionUi.showLoading());
  const { id, newUser } = payload;
  const { name, email, phone } = newUser;
  let fd = new FormData();
  fd.append("avatar", newUser.avatar, newUser.avatar.name);
  fd.append("name", newUser.name);
  fd.append("email", newUser.email);
  fd.append("phone", newUser.phone);

  const res = yield call(UserApi.updateUser, id, fd);
  if (res.status === 200) {
    yield put(actionUser.fetchListUser());
    yield put(actionUser.updateUserSuccess());
    yield put(actionUi.showAlert(1, "sá»­a thÃ nh viÃªn thÃ nh cÃ´ng"));
  } else {
    yield put(actionUser.updateUserFailed());
    yield put(actionUi.showAlert(4, "sá»­a thÃ nh viÃªn tháº¥t báº¡i"));
  }
  yield delay(3000);
  yield put(actionUi.hideAlert());
}


function* watchAddUserAction({ payload }) {
  yield put(actionUi.showLoading());
  const { newUser } = payload;
  const { avatar, name, email, phone,password } = newUser;
  console.log(newUser);

  let fd = new FormData();
  fd.append("avatar", avatar, avatar.name);
  fd.append("name", name);
  fd.append("email", email);
  fd.append("phone", phone);
  fd.append("password", password);
  console.log(newUser);
  const res = yield call(UserApi.postUser, fd);
  if (res.status === 201) {
    yield put(actionUser.fetchListUser());
    yield put(actionUser.addUserSuccess());
    yield put(actionUi.showAlert(1, "add new user success"));
  } else {
    yield put(actionUser.addUserFailed());
    yield put(actionUser.fetchListUser());
    yield put(actionUi.showAlert(4, "add new user failed"));
  }
  yield delay(3000);
  yield put(actionUi.hideAlert());
}


export function* todoUser() {
  yield takeEvery(UserTypes.FETCH_LIST_USER, watchFetchListUserAction);
  yield takeEvery(UserTypes.DELETE_USER, watchDeleteUserAction);
  yield takeEvery(UserTypes.UPDATE_USER, watchUpdateUserAction);
  yield takeEvery(UserTypes.ADD_USER, watchAddUserAction);

}

function* rootSaga() {
  yield all([fork(todoUser)]);
}

export default rootSaga;
