import React, { Component } from "react";
import { connect } from "react-redux";

import { Grid, Button } from "@material-ui/core";
import { withStyles } from "@material-ui/styles";
import AddIcon from "@material-ui/icons/Add";

import UserFormAdd from "./UserFormAdd";

import styles from "./styles";
import TaskItem from "../UserItem";
import * as actionUser from "../../actions/user";
import * as actionModal from "../../actions/modal";

class UserList extends Component {
  deleteUser = (id) => {
    this.props.deleteUser(id);
    this.props.hideModal();
  };

  updateUser = (id, newUser) => {
    this.props.updateUser(id, newUser);
    this.props.hideModal();
  };

  addUser = (newUser) => {
    // this.props.addUser(newUser);
    console.log("vao day");
    this.props.showModal();
    this.props.changeModalTitle("ThÃªm thÃ nh viÃªn");
    this.props.changeModalContent(
      <UserFormAdd  addUser={this.props.addUser}/>
    );
  };

  componentDidMount() {
    this.props.fetchListUser();
  }

  render() {
    const { users } = this.props;
    users.reverse();
    let elmTasks = users.map((user, index) => {
      return (
        <Grid item xs={12} md={4} lg={3} key={index}>
          <TaskItem
            user={user}
            deleteUser={this.deleteUser}
            updateUser={this.updateUser}
          />
        </Grid>
      );
    });

    return (
      <div>
        <Button
          variant="contained"
          color="primary"
          style={{ marginBottom: "20px" }}
          onClick={this.addUser}
        >
          ThÃªm thÃ nh viÃªn <AddIcon />
        </Button>
        <Grid container spacing={3}>
          {elmTasks}
        </Grid>
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    users: state.users.listUser,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    fetchListUser: () => {
      dispatch(actionUser.fetchListUser());
    },
    deleteUser: (userId) => {
      dispatch(actionUser.deleteUser(userId));
    },
    updateUser: (id, newUser) => {
      dispatch(actionUser.updateUser(id, newUser));
    },
    addUser: (newUser) => {
      dispatch(actionUser.addUser(newUser));
    },
    showModal: (user) => {
      dispatch(actionModal.showModal(user));
    },
    hideModal: () => {
      dispatch(actionModal.hideModal());
    },
    changeModalTitle: (title) => {
      dispatch(actionModal.changeModalTitle(title));
    },
    changeModalContent: (component) => {
      dispatch(actionModal.changeModalContent(component));
    },
  };
};

export default withStyles(styles)(
  connect(mapStateToProps, mapDispatchToProps)(UserList)
);
