import React, { Component } from "react";
import { connect } from "react-redux";

import {
  Button,
  TextField,
  FormControl,
  InputLabel,
  NativeSelect,
} from "@material-ui/core";

import MoreVertIcon from "@material-ui/icons/MoreVert";
import DeleteIcon from "@material-ui/icons/Delete";
import CreateIcon from "@material-ui/icons/Create";
import { withStyles } from "@material-ui/styles";

import * as actionModal from "../../actions/modal";

import styles from "./styles";

class CarItem extends Component {
  onDeleteCar = (car) => {
    this.props.showModal(car);
    this.props.changeModalTitle("XÃ³a xe nÃ y");
    this.props.changeModalContent(
      <div>
        <Button color="primary" onClick={this.props.hideModal}>
          Disagree
        </Button>
        <Button color="primary" autoFocus onClick={() => this.props.deleteCar(car.id)}>
          Agree
        </Button>
      </div>
    );
  };

  onUpdateCar = (car) => {
    this.props.showModal(car);
    this.props.changeModalTitle("Chá»nh sá»­a xe");
    this.props.changeModalContent(
      <div>
      <TextField
        id="standard-full-width"
        label="address"
        style={{ margin: 8 }}
        placeholder={car.address}
        fullWidth
        margin="normal"
        InputLabelProps={{
          shrink: true,
        }}
      />
      <TextField
        id="standard-full-width"
        label="Biá»n sá»"
        style={{ margin: 8 }}
        placeholder={car.license_plates}
        fullWidth
        margin="normal"
        InputLabelProps={{
          shrink: true,
        }}
      />
      <TextField
        id="standard-full-width"
        label="Price"
        style={{ margin: 8 }}
        placeholder={`${car.price}`}
        fullWidth
        margin="normal"
        InputLabelProps={{
          shrink: true,
        }}
      />
        <FormControl
          fullWidth
          style={{ margin: 8 }}
          margin="normal"
        >
          <InputLabel shrink htmlFor="age-native-label-placeholder">
          Type
          </InputLabel>
          <NativeSelect
            inputProps={{
              name: "age",
              id: "age-native-label-placeholder",
            }}
          >
            <option value="">None</option>
            <option value={10}>family</option>
            <option value={20}>travel</option>
            <option value={30}>office</option>
          </NativeSelect>
        </FormControl>
        
        <Button color="primary" onClick={this.props.hideModal}>
          Disagree
        </Button>
        <Button color="primary" autoFocus>
          Agree
        </Button>
      </div>
    );
  };

  render() {
    const { classes, car } = this.props;
    return (
      <div className="text-center">
        <Button
          variant="contained"
          color="primary"
          onClick={() => {
            this.onUpdateCar(car);
          }}
        >
          Sá»­a <CreateIcon />
        </Button>
        &nbsp;
        <Button
          variant="contained"
          color="secondary"
          onClick={() => {
            this.onDeleteCar(car);
          }}
        >
          XÃ³a <DeleteIcon />
        </Button>
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return {};
};

const mapDispatchToProps = (dispatch) => {
  return {
    showModal: (user) => {
      dispatch(actionModal.showModal(user));
    },
    hideModal: () => {
      dispatch(actionModal.hideModal());
    },
    changeModalTitle: (title) => {
      dispatch(actionModal.changeModalTitle(title));
    },
    changeModalContent: (component) => {
      dispatch(actionModal.changeModalContent(component));
    },
  };
};

export default withStyles(styles)(
  connect(mapStateToProps, mapDispatchToProps)(CarItem)
);
