import * as types from '../constants/user';

const initialState = {
    listUser: []
}

const reducer = (state = initialState, action) => {
    switch (action.type) {
        case types.FETCH_LIST_USER: {
            return {
                ...state,
                listUser: [],
            }
        }
        case types.FETCH_LIST_USER_SUCCESS: {
            const { data } = action.payload;
            const users = Object.values(data)
            return {
                ...state,
                listUser: users,
            }
        }
        case types.FETCH_LIST_USER_FAILED: {
            const { error } = action.payload;
            console.log(error);

            return {
                ...state,
                listUser: [],
            }
        }
        case types.ADD_USER:
            console.log("add user reducer");
            return {
                ...state,
            }
        case types.ADD_USER_SUCCESS:
            // const { task } = action.payload;
            // state.listUser = [task].concat(state.listUser);
            return {
                ...state
            }
        case types.ADD_USER_FAILED:
            return {
                ...state
            }
        case types.DELETE_USER:

            return {
                ...state
            }
        case types.DELETE_USER_SUCCESS:
            return {
                ...state
            }
        case types.DELETE_USER_FAILED:
            return {
                ...state
            }
        case types.UPDATE_USER:
            
            return {
                ...state
            }
        case types.UPDATE_USER_SUCCESS:
            return {
                ...state
            }
        case types.UPDATE_USER_FAILED:
            return {
                ...state
            }
        default:
            return state;
    }
}

export default reducer;