const styles = (theme) => ({
    root: {
      width: '100%',
      position: 'fixed',
      zIndex: 100
    },
    alert:{
        position: 'absolute',
        right: '50px',
        top: '10px',
        animationName: 'fly-left-right',
        animationDuration: '3.3s'
    }
  })

export default styles;